.page-body {
  position: relative;

  height: calc(100% - 205px);
  min-height: calc(100vh - 205px);
  margin-top: 95px;
  margin-bottom: 110px;
  overflow: auto;

  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
  color: var(--main-color);

  background-image:
    -webkit-gradient(
      linear,
      left top, left bottom,
      from(var(--main-color)),
      color-stop(6%, transparent),
      color-stop(94%, transparent),
      to(var(--main-color))
    ),
    radial-gradient(
      ellipse at center,
      #f5f5f5,
      var(--bg-color) 60%
    );

  background-image:
    -webkit-linear-gradient(
      top,
      var(--main-color),
      transparent 6%,
      transparent 94%,
      var(--main-color) 100%
    ),
    -webkit-radial-gradient(
      center,
      ellipse,
      #f5f5f5,
      var(--bg-color) 60%
    );

  background-image:
    -o-linear-gradient(
      top,
      var(--main-color),
      transparent 6%,
      transparent 94%,
      var(--main-color) 100%
    ),
    -o-radial-gradient(
      center,
      ellipse,
      #f5f5f5,
      var(--bg-color) 60%
    );

  background-image:
    linear-gradient(
      to bottom,
      var(--main-color),
      transparent 6%,
      transparent 94%,
      var(--main-color) 100%
    ),
    radial-gradient(
      ellipse at center,
      #f5f5f5,
      var(--bg-color) 60%
    );
}

@media (max-width: 600px) {
  .page-body {
    height: calc(100% - 215px);
    min-height: calc(100vh - 215px);
    margin-top: 105px;
  }
}



