.products {
  padding: 20px 0;
}

.products__heading {
  margin: 0;
  margin-bottom: 10px;

  font-size: 36px;
  line-height: 40px;
  font-weight: 700;
  text-align: center;
}

.products__list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  padding: 15px;
  margin: 0 auto;
  overflow: auto;

  list-style-type: none;
}

.products__item {
  width: 300px;
  padding: 10px 0;
  margin-right: 20px;
  margin-bottom: 20px;

  border: 1px solid var(--main-color);
  border-radius: 20px;
  -webkit-box-shadow: 2px 2px 4px 0 var(--main-color);
          box-shadow: 2px 2px 4px 0 var(--main-color);

  -webkit-transition:
    -webkit-transform 0.2s ease-in,
    -webkit-box-shadow 0.2s ease-out;

  transition:
    -webkit-transform 0.2s ease-in,
    -webkit-box-shadow 0.2s ease-out;

  -o-transition:
    transform 0.2s ease-in,
    box-shadow 0.2s ease-out;

  transition:
    transform 0.2s ease-in,
    box-shadow 0.2s ease-out;

  transition:
    transform 0.2s ease-in,
    box-shadow 0.2s ease-out,
    -webkit-transform 0.2s ease-in,
    -webkit-box-shadow 0.2s ease-out;
}

.products__item:hover {
  -webkit-transform: scale(1.05, 1.05);
      -ms-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);

  -webkit-box-shadow: 4px 4px 10px 0 var(--main-color);

          box-shadow: 4px 4px 10px 0 var(--main-color);
}

.products__item:nth-child(3n + 3) {
  margin-right: 0;
}

.products__img-wraper {
  width: 100%;
  height: 230px;
  margin-bottom: 10px;

  text-align: center;
}

.products__button {
  width: 80%;
  margin: 0 auto;

  color: var(--main-color);

  border-color: var(--main-color);
}

.products__button:hover,
.products__button:focus {
  color: #fff;

  background-color: var(--main-color);
}

@media (max-width: 1020px) {
  .products__list {
    max-width: 930px;
  }

  .products__item:nth-child(3n + 3) {
    margin-right: 20px;
  }

  .products__item:nth-child(2n + 2) {
    margin-right: 0;
  }
}

@media (max-width: 700px) {
  .products__list {
    max-width: 610px;
  }

  .products__item {
    margin-right: 0;
  }

  .products__item:nth-child(3n + 3) {
    margin-right: 0;
  }

  .products__item:nth-child(2n + 2) {
    margin-right: 0;
  }
}




