.comments {
  padding: 10px 0;
}

.comments__button {
  width: 200px;
  margin: 0 auto;
  margin-bottom: 20px;

  color: var(--main-color);

  border-color: var(--main-color);
}

.comments__button:hover {
  color: #fff;

  background-color: var(--main-color);
}

.comments__auth {
  margin: 0;
  margin-bottom: 20px;

  font-size: 24px;
  line-height: 30px;
  text-align: center;
}

.comments__list {
  display: block;
  max-width: 660px;
  width: 100%;
  padding: 0;
  margin: 0 auto;

  list-style-type: none;
}

.comments__list--off {
  display: none;
}

.comments__item {
  padding: 10px;
  margin-bottom: 20px;

  border: 1px solid var(--main-color);
  border-radius: 20px;
  -webkit-box-shadow: 2px 2px 4px 0 var(--main-color);
          box-shadow: 2px 2px 4px 0 var(--main-color);
}

.comments__wraper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 20px;

  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
}

.comments__text {
  margin: 0;
  overflow-wrap: break-word;

  font-weight: 400;
}

@media (max-width: 460px) {
  .comments__wraper {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .comments__text {
    text-align: center;
  }
}



