.page-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  height: 95px;
  padding-top: 20px;

  color: #fff;

  background-image:
    -webkit-gradient(
      linear,
      left top, left bottom,
      from(var(--main-color)),
      to(var(--main-color-opacity))
    );

  background-image:
    -webkit-linear-gradient(
      top,
      var(--main-color),
      var(--main-color-opacity)
    );

  background-image:
    -o-linear-gradient(
      top,
      var(--main-color),
      var(--main-color-opacity)
    );

  background-image:
    linear-gradient(
      to bottom,
      var(--main-color),
      var(--main-color-opacity)
    );
}

.page-header__wraper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.page-header__title {
  margin: 0;

  font-size: 24px;
  font-weight: 700;
}

.page-header__light-it {
  display: inline-block;

  color: #fff;

  -webkit-transition:
    color 0.2s ease-in,
    -webkit-transform 0.2s ease-out;

  transition:
    color 0.2s ease-in,
    -webkit-transform 0.2s ease-out;

  -o-transition:
    color 0.2s ease-in,
    transform 0.2s ease-out;

  transition:
    color 0.2s ease-in,
    transform 0.2s ease-out;

  transition:
    color 0.2s ease-in,
    transform 0.2s ease-out,
    -webkit-transform 0.2s ease-out;
}

.page-header__light-it:hover,
.page-header__light-it:focus {
  -webkit-transform: scale(1.1, 1.1);
      -ms-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);

  color: #8fbfe5ed;
  text-decoration: none;
}

.page-header__user {
  width: 500px;
}

@media (max-width: 600px) {
  .page-header {
    height: 105px;
    padding-top: 10px;
  }

  .page-header__wraper {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .page-header__title {
    margin: 0;
    margin-bottom: 10px;

    font-size: 18px;
    font-weight: 400;
  }

  .page-header__user {
    width: 100%;
  }
}



