.page-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;

  height: 110px;
  padding-top: 20px;

  color: #fff;

  background-image:
    -webkit-gradient(
      linear,
      left bottom, left top,
      from(var(--main-color)),
      to(var(--main-color-opacity))
    );

  background-image:
    -webkit-linear-gradient(
      bottom,
      var(--main-color),
      var(--main-color-opacity)
    );

  background-image:
    -o-linear-gradient(
      bottom,
      var(--main-color),
      var(--main-color-opacity)
    );

  background-image:
    linear-gradient(
      to top,
      var(--main-color),
      var(--main-color-opacity)
    );
}

.page-footer__author {
  display: block;
  margin-bottom: 10px;

  font-size: 24px;
  font-weight: 700;
  text-align: center;
}

.page-footer__contacts-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0;
  margin: 0;

  list-style-type: none;
}

.page-footer__contacts-item {
  margin-right: 20px;
}

.page-footer__contacts-item:last-child {
  margin-right: 0;
}

.page-footer__link {
  display: block;

  color: #fff;

  -webkit-transition:
    color 0.2s ease-in,
    -webkit-transform 0.2s ease-out;

  transition:
    color 0.2s ease-in,
    -webkit-transform 0.2s ease-out;

  -o-transition:
    color 0.2s ease-in,
    transform 0.2s ease-out;

  transition:
    color 0.2s ease-in,
    transform 0.2s ease-out;

  transition:
    color 0.2s ease-in,
    transform 0.2s ease-out,
    -webkit-transform 0.2s ease-out;
}

.page-footer__link:hover,
.page-footer__link:focus {
  -webkit-transform: scale(1.1, 1.1);
      -ms-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);

  color: #8fbfe5ed;
  text-decoration: none;
}



